import { assert } from "@thi.ng/api";
import { illegalState } from "@thi.ng/errors";
import { setInUnsafe, updateInUnsafe } from "@thi.ng/paths";
import { nextID } from "./idgen";
/**
 * Return a new {@link Transacted} state wrapper.
 *
 * @param parent
 */
export const defTransacted = (parent) => new Transacted(parent);
/**
 * Like {@link defTransacted}, but immediately starts new transaction as
 * well, i.e. same as `defTransacted(state).begin()`.
 *
 * @param parent
 */
export const beginTransaction = (parent) => new Transacted(parent).begin();
export class Transacted {
    constructor(parent) {
        this.parent = parent;
        this.current = undefined;
        this.isActive = false;
        this.id = `tx-${nextID()}`;
    }
    get value() {
        return this.deref();
    }
    set value(val) {
        this.reset(val);
    }
    get isTransaction() {
        return this.isActive;
    }
    deref() {
        return this.isActive ? this.current : this.parent.deref();
    }
    equiv(o) {
        return this === o;
    }
    reset(val) {
        this.ensureTx();
        this.current = val;
        return val;
    }
    resetIn(path, val) {
        this.ensureTx();
        return (this.current = setInUnsafe(this.current, path, val));
    }
    resetInUnsafe(path, val) {
        return this.resetIn(path, val);
    }
    swap(fn, ...args) {
        this.ensureTx();
        return (this.current = fn.apply(null, [this.current, ...args]));
    }
    swapIn(path, fn, ...args) {
        this.ensureTx();
        return (this.current = updateInUnsafe(this.current, path, fn, ...args));
    }
    swapInUnsafe(path, fn, ...args) {
        return this.swapIn(path, fn, ...args);
    }
    begin() {
        assert(!this.isActive, "transaction already started");
        this.current = this.parent.deref();
        this.isActive = true;
        this.parent.addWatch(this.id + "--guard--", () => illegalState(`${this.id} parent state changed during active transaction`));
        return this;
    }
    commit() {
        const val = this.current;
        this.cancel();
        return this.parent.reset(val);
    }
    cancel() {
        this.ensureTx();
        this.parent.removeWatch(this.id + "--guard--");
        this.current = undefined;
        this.isActive = false;
    }
    addWatch(id, watch) {
        return this.parent.addWatch(this.id + id, (_, prev, curr) => watch(id, prev, curr));
    }
    removeWatch(id) {
        return this.parent.removeWatch(this.id + id);
    }
    notifyWatches(old, curr) {
        this.parent.notifyWatches(old, curr);
    }
    release() {
        delete this.parent;
        delete this.current;
        delete this.isActive;
        delete this._watches;
        return true;
    }
    ensureTx() {
        assert(this.isActive, "no active transaction");
    }
}
